<template>
  <div>
    <t-drawer
      header="附件详情"
      :visible="visible"
      :onClose="close"
      :closeBtn="true"
      size="400px"
      :onOverlayClick="close"
      placement="right"
    >
      <t-list :split="true">
        <t-list-item>
          <t-list-item-meta
            :image="imageUrl"
            title="列表标题"
            description="列表内容列表内容"
          ></t-list-item-meta>
          <template #action>
            <t-button variant="text" shape="square"><edit-icon /></t-button>
            <t-button variant="text" shape="square"><download-icon /></t-button>
          </template>
        </t-list-item>
        <t-list-item>
          <t-list-item-meta
            :image="imageUrl"
            title="列表标题"
            description="列表内容列表内容"
          ></t-list-item-meta>
          <template #action>
            <t-button variant="text" shape="square"><edit-icon /></t-button>
            <t-button variant="text" shape="square"><download-icon /></t-button>
          </template>
        </t-list-item>
      </t-list>
      <template #footer>
        <t-button @click="deleteAttach">删除</t-button>
      </template>
    </t-drawer>
  </div>
</template>

<script>
import { EditIcon, DownloadIcon } from "tdesign-icons-vue";
export default {
  name: "AttachDetailDrawer",
  data() {
    return {
      imageUrl:
        "https://alkaidchen.oss-cn-guangzhou.aliyuncs.com/site/cover.png",
    };
  },
  computed: {
    visible() {
      return this.$store.state.app.attachDetailDrawerVisible;
    },
  },
  methods: {
    close() {
      this.$store.commit("CLOSE_ATTACH_DETAIL_DRAWER");
    },
    // 点击加载更多，状态切换为「加载中」
    loadMore() {
      this.asyncLoadingRadio = "loading";
    },
    deleteAttach() {
      this.$message.info("未实现")
    },
  },
  watch: {
    // 也可以使用插槽自定义加载内容
    asyncLoadingRadio(val) {
      if (val === "loading-custom") {
        this.asyncLoading = () => <div> 没有更多数据了 </div>;
      } else {
        this.asyncLoading = this.asyncLoadingRadio;
      }
    },
  },
  components: { EditIcon, DownloadIcon },
};
</script>

<style lang="less" scoped>
.image-wrapper {
  margin: 0 auto;
  justify-content: center;
  img {
    height: 175px;
    align-items: center;
    margin: 0 auto;
    border-radius: 5px;
  }
}
</style>